<script setup>
import { ref, onMounted, onUnmounted, defineEmits, computed } from 'vue';
import store from '@/vuex';

const shortWallet = computed(() => {
  return store.getters.walletAddress ? store.getters.walletAddress.slice(0, 4) + '...' + store.getters.walletAddress.slice(-4) : 'not connected';
});

const isOpen = ref(false);
const forceHide = ref(false);
const isAiEnabled = ref(true);
const isMobileMenuOpen = ref(false);
const isBitcatMenuOpen = ref(false);
const isBitcatMobileMenuOpen = ref(false);

// Простые переменные для темы и шрифта
const isDarkTheme = ref(localStorage.getItem('theme') === 'dark');
const isSerious = ref(localStorage.getItem('font') !== 'fun');

const emit = defineEmits(['toggleAi']);

let clickHandler;

onMounted(() => {
  const aiEnabled = localStorage.getItem('bitcat_ai_enabled');
  isAiEnabled.value = aiEnabled === null ? true : aiEnabled === 'true';
  
  // Устанавливаем serious шрифт по умолчанию
  const savedFont = localStorage.getItem('font');
  if (!savedFont) {
    localStorage.setItem('font', 'serious');
    document.body.classList.add('font-serious');
  }
  
  // Close dropdown when clicking outside
  clickHandler = (event) => {
    const dropdown = event.target.closest('.dropdown');
    if (!dropdown) {
      isOpen.value = false;
    }
  };
  document.addEventListener('click', clickHandler);
});

onUnmounted(() => {
  if (clickHandler) {
    document.removeEventListener('click', clickHandler);
  }
});

const toggleAi = () => {
  isAiEnabled.value = !isAiEnabled.value;
  localStorage.setItem('bitcat_ai_enabled', isAiEnabled.value.toString());
  emit('toggleAi', isAiEnabled.value);
};

const toggleMenu = () => {
  isOpen.value = !isOpen.value;
  if (isOpen.value) {
    forceHide.value = false;
  }
};

const closeMenu = () => {
  isOpen.value = false;
  forceHide.value = true;
  // Reset force hide after animation
  setTimeout(() => {
    forceHide.value = false;
  }, 500);
};

const toggleMobileMenu = () => {
  isMobileMenuOpen.value = !isMobileMenuOpen.value;
  // Prevent body scrolling when menu is open
  document.body.style.overflow = isMobileMenuOpen.value ? 'hidden' : '';
};

const toggleBitcatMenu = () => {
  isBitcatMenuOpen.value = !isBitcatMenuOpen.value;
};

const closeMobileMenu = () => {
  isMobileMenuOpen.value = false;
  document.body.style.overflow = '';
};

const closeBitcatMenu = () => {
  isBitcatMenuOpen.value = false;
};

const toggleBitcatMobileMenu = () => {
  isBitcatMobileMenuOpen.value = !isBitcatMobileMenuOpen.value;
  // Prevent body scrolling when menu is open
  document.body.style.overflow = isBitcatMobileMenuOpen.value ? 'hidden' : '';
};

const closeBitcatMobileMenu = () => {
  isBitcatMobileMenuOpen.value = false;
  document.body.style.overflow = '';
};

// Простые функции переключения
const toggleTheme = () => {
  isDarkTheme.value = !isDarkTheme.value;
  if (isDarkTheme.value) {
    document.documentElement.setAttribute('data-theme', 'dark');
    localStorage.setItem('theme', 'dark');
  } else {
    document.documentElement.removeAttribute('data-theme');
    localStorage.setItem('theme', 'light');
  }
};

const toggleFont = () => {
  isSerious.value = !isSerious.value;
  if (isSerious.value) {
    document.body.classList.add('font-serious');
    localStorage.setItem('font', 'serious');
  } else {
    document.body.classList.remove('font-serious');
    localStorage.setItem('font', 'fun');
  }
};
</script>

